.color {
  color: #3ee9e5;
}

.month {
  font-size: 1.125rem;
  opacity: 0.6;
  font-weight: 400;
}

body {
  box-sizing: border-box;
  background-color: #080c20;
}

h1,
h2,
h3,
h4 {
  color: #fff;
}

p {
  color: #777f98;
}

header,
section,
footer {
  text-align: center;
  padding: 1rem;
}

section,
footer {
  margin-top: 9rem;
}

footer {
  margin-bottom: 5rem;
}

@media screen and (min-width: 1200px) {
  main {
    max-width: 1100px;
    margin: 0 auto;
  }
}

body {
  font-family: "Manrope", sans-serif;
  line-height: 1.5;
}

h1 {
  font-size: 2rem;
}
@media screen and (min-width: 1200px) {
  h1 {
    font-size: 3rem;
  }
}

h2 {
  font-size: 1.125rem;
}

h3 {
  font-size: 1.5rem;
}

h4 {
  font-size: 3rem;
  margin: 1.5rem auto 1.25rem;
}

p {
  line-height: 1.8;
}

p,
button,
input,
::placeholder {
  font-family: inherit;
  font-size: 1rem;
}

.benefits__card:first-child img {
  margin-right: -0.7rem;
}
.benefits__card:nth-child(2) img {
  width: 60px;
}
.benefits__card:nth-child(4) img {
  width: 78px;
}
.benefits__card--img-container {
  width: 120px;
  height: 124px;
  margin: 0 auto;
  background-color: #093f68;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 45px;
}
.benefits__card--img-container img {
  width: 70px;
}
@media screen and (min-width: 1200px) {
  .benefits__card--img-container {
    width: 160px;
    height: 164px;
  }
}
.benefits__card--content__title {
  margin: 2rem auto 0.687rem;
}

.pricing__plans--card {
  border-radius: 8px;
  text-align: left;
  padding: 0 1.5rem 1.5rem;
}
.pricing__plans--card__title {
  margin-top: 1.5rem;
}
.pricing__plans--card__top {
  margin-top: -1.4rem;
}
.pricing__plans--card p {
  color: #fff;
}
.pricing__plans--card__para {
  color: #fff;
  opacity: 0.7;
}
.pricing__plans--card.free {
  background-color: #093f68;
}
.pricing__plans--card.free svg path {
  fill: #3ee9e5;
}
.pricing__plans--card.premium {
  background-color: #3ee9e5;
}
.pricing__plans--card.premium h2,
.pricing__plans--card.premium h4,
.pricing__plans--card.premium p {
  color: #080c20;
}
.pricing__plans--card.premium__para {
  opacity: 0.6;
}
.pricing__plans--card.premium svg path {
  fill: #080c20;
}

.logo-container {
  max-width: 1100px;
  margin: 2.5rem auto 4.125rem;
}
@media screen and (min-width: 768px) {
  .logo-container {
    margin: 3rem auto 4.812rem 2.5rem;
    text-align: left;
  }
}
@media screen and (min-width: 1200px) {
  .logo-container {
    margin: 3rem auto;
  }
}

.background {
  background: url(../assets/illustration-hero-mobile.png);
  height: 270px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
@media screen and (min-width: 360px) {
  .background {
    background-position: left top;
    background-size: 109%;
    max-width: 480px;
    margin: 0 auto;
  }
}

.heading__content--title {
  margin: 3rem auto 1.5rem;
}
.heading__content--para {
  margin-bottom: 2rem;
}

.benefits {
  display: flex;
  flex-direction: column;
  gap: 3.4rem;
}

.pricing__plans--card__benefits--row {
  display: flex;
  justify-content: start;
  align-items: center;
  gap: 0.95rem;
}
.pricing__plans--card__benefits--row p {
  margin: 0.5rem 0;
}

.pricing__content--para {
  margin: 1.575rem auto 4.5rem;
}
.pricing__plans {
  display: flex;
  flex-direction: column;
  gap: 4rem;
}

.pricing__plans--table__title {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.subscribe {
  display: flex;
  flex-direction: column;
}
.subscribe__title {
  background: url("../assets/bg-footer-squiggle.svg") center/120% 100% no-repeat;
  padding: 1rem 3.5rem;
}